import "../../styles/AdminDash.css";
import "../../styles/CreateCategory.css";
import React, { useEffect, useState } from "react";
import AdminMenu from "../../components/AdminMenu";
import toast from "react-hot-toast";
import axios from "axios";
import { MdDelete } from "react-icons/md";
import CategoryForm from "../../components/CategoryForm";
import { Modal } from "antd";

const CreateCategory = () => {
  const [categories, setCategories] = useState([]);
  const [name, setName] = useState("");
  const [visible, setVisible] = useState(false);
  const [selected, setSelected] = useState(null);
  const [updatedName, setUpdatedName] = useState("");

  // handel form
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.post(
        `/api/v1/category/create-category`,
        { name }
      );

      if (data?.success) {
        toast.success(`${name} is created`);
        getAllCategory();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      toast.error("Something went wrong in Input form");
    }
  };

  // get all category
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get(
        `/api/v1/category/allcategory`
      );
      if (data?.success) {
        setCategories(data?.category);
      }
    } catch (error) {
      toast.error("Something went wrong getting category");
    }
  };

  useEffect(() => {
    getAllCategory();
  }, []);

  // update category
  const handleUpdate = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.put(
        `/api/v1/category/update-category/${selected._id}`,
        { name: updatedName }
      );

      if(data.success){
        toast.success(`${updatedName} successfully updated`);
        setSelected(null);
        setUpdatedName("")
        setVisible(false);
        getAllCategory();
      }else{
        toast.error(data.message);
      }

    } catch (error) {
      toast.error("Something went wrong updating category");
    }
  };

  // delete category
  const handleDelete = async (pid) => {
    try {
      const { data } = await axios.delete(
        `/api/v1/category/deletecategory/${pid}`);

      if(data.success){
        toast.success('Category Successfully Deleted');
        getAllCategory();
      }else{
        toast.error(data.message);
      }

    } catch (error) {
      toast.error("Something went wrong updating category");
    }
  };

  return (
    <div className="admin_page">
      <div className="admin_menu">
        <AdminMenu />
      </div>
      <div className="admin_right">
        <div className="admin_right_form">
          <h2> Create a new category </h2>
          <CategoryForm
            handleSubmit={handleSubmit}
            value={name}
            setValue={setName}
          />
        </div>
        <h2> all category </h2>
        <table>
          <thead>
            <tr>
              <th>Name</th>
              <th>Edit</th>
              <th>Remove</th>
            </tr>
          </thead>
          <tbody>
            {categories?.map((c) => {
              return (
                <>
                  <tr>
                    <td key={c._id} className="name">
                      {c.name}
                    </td>
                    <td className="edit">
                      <button
                        onClick={() => {
                          setVisible(true);
                          setUpdatedName(c.name);
                          setSelected(c)
                        }}
                      >
                        edit
                      </button>
                    </td>
                    <td className="remove">
                      <button onClick={() => {handleDelete(c._id)}}>
                        <MdDelete />
                      </button>
                    </td>
                  </tr>
                </>
              );
            })}
          </tbody>
        </table>
        <Modal onCancel={() => setVisible(false)} footer={null} open={visible}>
          <CategoryForm
            value={updatedName}
            setValue={setUpdatedName}
            handleSubmit={handleUpdate}
          />
        </Modal>
      </div>
    </div>
  );
};

export default CreateCategory;
